export const generatePics =function () {
  const requestUrl = [];

  for (let i = 0; i < COUNT_PICS; i++) {

    requestUrl.push(`http://picsum.photos/248/152?${(Math.floor(10 * Math.random()))}`);

  }
  return requestUrl;
};
